.header{
    padding-top: 26px;
    padding-bottom: 102px;
    background-color: #f1b400;
    background-image: url(/src/images/png/header@1x-min.png);
    background-position: right;
    background-size: 454px;
    background-repeat: no-repeat;
    &__wrapper{
        display: flex;
    }
    &__logo-wrap{
        width: 88px;
        margin: 26px 75px;
    }
    &__nav{
        display: flex;
    }
    &__sections{
        display: flex;
        justify-content: space-around;
    }
    &__link{
        font-family: "Neucha", sans-serif;
font-weight: 400;
font-size: 24px;
color: #fff;
cursor: pointer;
    }
    &__langs{
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
    }
    &__lang-btn{
        background-color: transparent;
        border: none;
        font-family: "Neucha", sans-serif;
font-weight: 400;
font-size: 24px;
color: #fff;
cursor: pointer;
    }
    &__phone-wrap{
        display: flex;
        justify-content: center;
        font-family: "Neucha", sans-serif;
        font-weight: 400;
        font-size: 24px;
        color: #fff;
        cursor: pointer;
    }
    &__icon{
width: 31px;
height: 31px;
fill: #fff;
    }
    &__title{
        font-weight: 400;
font-size: 150px;
color: #fff;
font-family: "Neucha", sans-serif;
margin-top: 30px;
margin-bottom: 47px;
    }
    &__description{
        font-weight: 400;
font-size: 70px;
color: #fff;
font-family: "Neucha", sans-serif;
margin-bottom: 50px;
    }
    &__buttons{
        display: flex;
    }
    &__button{
        font-family: "Neucha", sans-serif;
        font-weight: 400;
        font-size: 39px;
        color: #fff;
        padding: 23px 66px;
        border: none;
        border-radius: 10px;
        background-color: #E2461F;
    }
    &__video{
        display: flex;
        flex-direction: column;
        justify-content: center;
        font-family: "Neucha", sans-serif;
        font-weight: 400;
font-size: 20px;
text-align: center;
color: #fff;
    }
    &__video-icon{
    width: 35px;
    height: 35px;    
}
}